.container {
    position: absolute;
    top: 0;
    right: 0;
    width: 300px;
    height: 50px;
    background-color: #f1392d;
    color: white;
    font-size: 20px;
    padding: 20px 20px;
    text-align: center;
    vertical-align: center;
    margin-top: 10px;
    margin-right: 30px;
  }
  .text {
    line-height: 0.5;
  }
  .enter {
    opacity: 0;
    transform: translateX(100%);
    transition: all 250ms linear;
  }
  
  .enterActive {
    opacity: 1;
    transform: translateX(0);
  }
  
  .exit {
    opacity: 1;
    transform: translateX(0);
    transition: all 250ms linear;
  }
  .exitActive {
    opacity: 0;
    transform: translateX(100%);
  }