.title {
    text-transform: capitalize;
    font-size: 25px;
    margin-bottom: 10px;
    color: #3544a9;
    text-align: center;
  }
  
  .appear {
    opacity: 0;
    transform: translateY(-100%);
    transition: all 500ms linear;
  }
  
  .appearActive {
    opacity: 1;
    transform: translateY(0);
  }